.intro { 
    max-width: 580px;
    text-align: center;
    margin: auto;
}
.intro > .profile {
    display: inline-block;
    border-radius: 50%; 
    overflow: hidden;
}
.intro > .full-name {
    margin-top: 0.25rem;
}
.intro > .work-status {
    font-weight: lighter;
    font-size: 1.25rem;
    margin-top: 0.5rem;
}
.title-with-icon {
    position: relative;
    margin-left: 20px;
    margin-bottom: 40px;
}
.title-with-icon:before {
    content: "\E847";
    font-family: mytabworks;
    position: absolute;
    top: 0;
    left: 0;
    font-size: 40px;
    color: rgba(154, 154, 154, 0.3);
    transform: translate(-50%, -20%);
    z-index: -1;
}
.title-with-icon:not(.hidden):before {
    animation: title-anime 1.2s cubic-bezier(1, 0.78, 1, 0.76);
}
@keyframes title-anime {
    from {
        font-size: 400px;
        opacity: 0;
    }
    to {
        font-size: 40px;
        opacity: 1;
    }
}
.social-media {
    margin-top: 20px;
}
.social-media > .n-btn {
    display: inline-flex;
    justify-content: center;
    align-items: center;
    height: 40px;
    width: 40px;
    font-size: 20px;
}
.social-media > .n-btn:not(:last-child) {
    margin-right: 7px;
}
.wave-length {
    width: 50vw;
    height: 50vw;
    border-radius: 50%;
    transition: transform 5s ease-out, opacity 1.5s ease-out 3.5s;
    transform: scale(0);
    will-change: transform, opacity, border-radius, box-shadow;
    z-index: -1
}
.gigantic {
    display: block;
    min-height: 100%;
}
.gigantic > .gigantic-context {
    position: relative;
    z-index: 1;
    /* mix-blend-mode: multiply; */
} 
.gigantic::before {
    font-family: mytabworks;
    content: "\E801"; 
    position: absolute;
    left: 0;
    bottom: 0;
    transform: translate(-40%, 219%);
    font-size: 661px;
}
.jumbocont {
    width: 100%;
    border-radius: 15px;
    padding: 80px 60px;
}
.context {
    line-height: 2;
    font-size: 15px;
}
.progress-wrapper:not(:last-child) {
    margin-bottom: 30px;
}
.progress-wrapper .n-progress-bar {
    height: 10px;
}
.progress-wrapper > .progress-label {
    display: block;
    font-size: 15px;
    font-weight: 600;
    margin: 4px 0 10px;
}
.progress-wrapper .n-progress-bar:not(.hidden) {
    animation: progress 500ms ease-in;
}
@keyframes progress {
    from {
        width: 0%;
    }
}
#home {
    display: flex;
    align-items: center;
    position: relative;
    height: 100vh;
}
#home > .scroll-down,
#home > .scroll-down:before,
#home > .scroll-down:after {
    position: absolute;
    bottom: 70px;
    left: 50%;
    transform: translateX(-50%);
}
#home > .scroll-down:before,
#home > .scroll-down:after {
    content: "";
    display: block;  
    border-radius: 30px;
}
#home > .scroll-down:before {
    bottom: -40px; 
    height: 30px;
    width: 25px; 
}
#home > .scroll-down:after {
    bottom: -20px; 
    width: 5px;
    height: 6px;
    border-width: 1px !important;
    animation: scroll-down 2s infinite ease-in-out;
}
@keyframes scroll-down {
    0%, 100% {
        transform: translate(-50%, 0);
    }
    80% {
        transform: translate(-50%, 6px);
    }
}
#home img {
    padding: 30px;
    width: 180px;
    height: 180px; 
}
#about-me,
#practice,
#skills,
#contact-me {
    padding: 100px 0 50px;
}
#skills img{
 width: 100%;
}
#footer {
    margin: 30px 0;
}
#about-me > :first-child:hover .morph-flat, 
#skills .jumbocont:hover .icon-card-wrapper.n-engrave,
#skills .jumbocont:hover .icon-card-wrapper.n-engrave > .icon-card {
    will-change: border-radius, box-shadow;
}
#skills img[alt="php"] {
    filter: grayscale(1) opacity(0.5) !important;
}
#contact-me .n-form-control,
#contact-me .n-btn  {
    font-size: 17px;
    padding: 15px 30px;
    border-radius: 50px;
}

#contact-me .n-invalid-feedback,
#contact-me .n-valid-feedback {
    font-style: italic;
    text-align: center;
}

#contact-me .n-btn {
    display: block;
    margin-left: auto;
}
#about-me #content {
    padding: 25px;
    border-radius: 15px;
}

@media only screen 
  and (min-width: 568px) {

    #about-me #content {
        height: auto;
        padding: 35px;
    }
}

@media only screen 
  and (max-device-width: 568px) {
    #contact-me .n-form-control, #contact-me .n-btn {
        font-size: 15px;
        padding: 10px 20px;
    }
}

/*tablet portrait*/
@media only screen 
  and (max-device-width: 769px) {
    .jumbocont {
        padding: 50px 30px;
    }
    .gigantic:before {
        transform: translate(-40%, 1227%);
    }
}
/*mobile portrait*/
@media only screen 
  and (max-device-width: 469px)  
  and (-webkit-min-device-pixel-ratio: 2) { 
    #about-me, #practice, #skills, #contact-me {
        padding: 80px 0 30px;
    }
    #home img {
        padding: 25px;
        width: 150px;
        height: 150px;
    }
    .jumbocont {
        padding: 50px 20px;
    }
    .gigantic {
        display: none;
    }
}
/*mobile*/
@media only screen 
and (min-device-width: 568px) 
and (max-device-width: 991px)
and (-webkit-min-device-pixel-ratio: 2)
and (orientation: landscape){ 
    #home {
        height: auto;
        padding-top: 110px;
    }
    #home > .scroll-down {
        display: none;
    }
}